              <button type="button" class="btn link1 widthauto">Grant Permission</button>
            </div>
          </div>
        </div>
      </div>
    </section>
    </main>
</template>
<script>
import {
    mapGetters
} from "vuex";
export default {
    name: "UserAddPermission",
    data() {
        return {
          countries:[],
          states:[],
          cities:[],
            editmode: false,
            step: 1,
            users: {},
            frm_user: new Form({
                id: '',
                organization: '',
                name: '',
                address_1: '',
                address_2: '',
                city: '',
                state: '',
                zip_code: '',
                country: 'united states',
                phone: '',
                email: '',
                fax: '',
                type: '',
                account_info: []
            })
        }
    },
    computed: {
        ...mapGetters(["errors"])
    },
        beforeCreate(){ 
          axios.get("api/getCountries" )
                .then((res) => {
                    this.countries=res.data.data
                })
                 
        },
    methods: {
        async addInfo() {
            this.frm_user.account_info.push({
                id: '',
                account_no: '',
            })
        },
        async next_step(final = false) {
            this.$validator.validateAll('frmStep' + this.step).then(async (valid) => {
                if (valid) {
                    this.step++
                    if (final == 'submit') {
                        this.step = 1;
                        this.submit_user();
                    }
                }
            })
        }, 

        submit_user() {
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_user
            });

            if (this.frm_user.id > 0) {
                this.frm_user.put('api/user/' + this.frm_user.id)
                    .then((response) => {
                        cloaderd.hide()
                        if (response.data.success == true) {
                            this.frm_user.reset();
                            Toast.fire({
                                icon: 'success',
                                title: response.data.message
                            });
                            this.$router.push('/users').catch(() => {});
                        }
                    })
                    .catch(() => {
                        cloaderd.hide();
                    })
            } else {
                this.frm_user.post('api/user')
                    .then((data) => {
                        cloaderd.hide()
                        this.frm_user.reset();
                        $('a[href="#menu1"]').tab('show');
                        this.frm_user.account_info = []
                        this.addInfo()
                        Toast.fire({
                            icon: 'success',
                            title: data.data.message
                        });

                    })
                    .catch(() => {
                        cloaderd.hide();
                    })
            }
        },



    },
    mounted() {
        if (this.$route.params.id) {
            this.frm_user.id = this.$route.params.id;
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_user
            });
            axios.get("api/user/" + this.$route.params.id)
                .then((res) => {
                    this.frm_user.fill(res.data.data)
                    cloaderd.hide();
                })
                .catch(() => {
                    cloaderd.hide();
                    this.$router.push('/users/').catch(() => {});
                })

        }
    },
    created() {

        this.$Progress.start();
        this.addInfo();
        this.$Progress.finish();
    },
    watch: {
        async step(n) {
            $('a[href="#menu' + n + '"]').tab('show');
        },
        async 'frm_user.organization'(n) {
            if (n == 'United Scrap Metal') {
                if (!['Admin', 'Finance Team', 'Transportation Team', 'Sales Team', 'CEC'].includes(this.frm_user.type)) {
                    this.frm_user.type = ''
                }
            } else {
                if (!['Primary', 'Finance', 'Transportation'].includes(this.frm_user.type)) {
                    this.frm_user.type = ''
                }
            }
        },
        'frm_user.country':{
          immediate: true,
          handler(n) {
            if(n){
                  axios.get("api/getStates",{
                    params:{'country':n}
                  } )
                      .then((res) => {
                          this.states=res.data.data
                      })
                }
          } 
      }, 
    }
}
</script>